.smoothies {
	padding: 100px 0 115px;

	@media only screen and (max-width: 960px) {
		padding: 48px 0 90px;
	}
&__wrapper {
	@include flex ($align-items: center, $flex-direction: column);
}
	&__title {
		text-align: center;
		margin-bottom: 40px;
		

		@media only screen and (max-width: 960px) {
			margin-bottom: 24px;
		}
	}

	&__text {
		max-width: 915px;
		text-align: center;
		margin-bottom: 40px;
		
	}

	&__cards {
		@include flex (center, center, wrap);
		max-width: 1050px;
		
		background-color: $bg-cards-color;

		@media only screen and (max-width: 960px) {
			max-width: 540px;
		}
	}

	&__item {
		@include flex (flex-end, center, $flex-direction: column);
		@include wh (25%, 195px);
		padding: 0 10px;
		background-color: $bg-main-color;

		&:nth-child(-n + 8) {
			border-right: 2px solid #f6edde;
		}

		&:nth-child(-n + 4) {
			border-bottom: 2px solid #f6edde;
		}

		&:nth-child(4),
		&:last-child {
			border-right: none;
		}

		@media only screen and (max-width: 960px) {
			width: 50%;

			&:nth-child(-n + 8),
			&:nth-child(-n + 4),
			&:nth-child(4),
			&:last-child {
				border: none;
			}

			&:nth-child(odd) {
				border-right: 2px solid #f6edde;
				border-bottom: 2px solid #f6edde;
			}

			&:nth-child(even) {
				border-bottom: 2px solid #f6edde;
			}

			&:nth-child(7),
			&:last-child {
				border-bottom: none;
			}
		}
	}
	

	&__desc {
		@include font (25px, 40px, $font-weight: 800, $text-align: center);
		margin: 27px 0 30px;
		white-space: nowrap;

		@media only screen and (max-width: 476px) {
			@include font (16px);
		}
	}
}